<template>
  <div class="col">
    <div class="panel">
      <h4>Encoded Base64</h4>
      <div class="list-group">
        <textarea class="textarea" v-model="encoded_b64_textarea"></textarea>
      </div> 
     </div> 
  </div>
  <div class="col-2">
    <div class="d-flex flex-column">
       <button @click="decode">Decode</button>
       <br><br>
       <button @click="encode">Encode</button>
       <br><br>
       <button @click="clear">Clear</button>
    </div> 
  </div> 
  <div class="col">
     <div class="panel">
      <h4>Decoded Base64</h4>
      <div class="list-group">
      <textarea class="textarea" v-model=decoded_b64_textarea></textarea>
      </div>
    </div>
  </div>
</template>

<style scoped>
#app {
  height: inherit;
}
h4 {
  text-align: center;
}
</style>

<script>
export default {
  data() {
    return {
      encoded_b64_textarea: '',
      decoded_b64_textarea: ''
    }
  }, 
  methods: {
    encode(event) {
      let encode_val = this.encoded_b64_textarea
      this.decoded_b64_textarea = btoa(encodeURIComponent(encode_val))
    },
    decode(event) {
      let decode_val = this.decoded_b64_textarea
      this.encoded_b64_textarea = atob(decodeURIComponent(decode_val))
    },
    clear() {
      this.decoded_b64_textarea = ''
      this.encoded_b64_textarea = ''
    }
  }
}
</script>
